<template><markdown-wrapper><h1 id="button">Button <a class="header-anchor" href="#button" aria-hidden="true">&#128279;</a></h1>
<h2 id="examples">Examples <a class="header-anchor" href="#examples" aria-hidden="true">&#128279;</a></h2>
<p>Use any of the available button types to quickly create a styled button.</p>
