<span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"link"</span>&gt;</span>Link<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-comment">&lt;!-- btn-examples.vue --&gt;</span>
</code></pre>
<h2 id="links">Links <a class="header-anchor" href="#links" aria-hidden="true">&#128279;</a></h2>
<p>Buttons with <code v-pre>href</code> or <code v-pre>to</code> prop will render as link tag.</p>
<btn-links ref="btn-links"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">h4</span>&gt;</span>Native links<span class="hljs-tag">&lt;/<span class="hljs-name">h4</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">href</span>=<span class="hljs-string">"#"</span>&gt;</span>Default<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">href</span>=<span class="hljs-string">"#"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>Primary<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">h4</span>&gt;</span>Vue Router links<span class="hljs-tag">&lt;/<span class="hljs-name">h4</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">to</span>=<span class="hljs-string">"/"</span>&gt;</span>Default<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">to</span>=<span class="hljs-string">"/"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>Primary<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-comment">&lt;!-- btn-links.vue --&gt;</span>
</code></pre>
<h2 id="sizes">Sizes <a class="header-anchor" href="#sizes" aria-hidden="true">&#128279;</a></h2>
<p>Fancy larger or smaller buttons? Add size <code v-pre>lg</code>, <code v-pre>sm</code>, or <code v-pre>xs</code> for additional sizes.</p>
<btn-sizes ref="btn-sizes"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"lg"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>Large button<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"lg"</span>&gt;</span>Large button<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>Default button<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">btn</span>&gt;</span>Default button<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"sm"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>Small button<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"sm"</span>&gt;</span>Small button<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"xs"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>Extra small button<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"xs"</span>&gt;</span>Extra small button<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
<span class="hljs-comment">&lt;!-- btn-sizes.vue --&gt;</span>
</code></pre>
<p>Create block level buttons—those that span the full width of a parent — by adding <code v-pre>block</code>.</p>
<btn-block ref="btn-block"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">block</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"lg"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>Block level button<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">block</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"lg"</span>&gt;</span>Block level button<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-comment">&lt;!-- btn-block.vue --&gt;</span>
</code></pre>
<h2 id="active-state">Active state <a class="header-anchor" href="#active-state" aria-hidden="true">&#128279;</a></h2>
<p>Add <code v-pre>active</code> to make buttons appear pressed (with a darker background, darker border, and inset shadow).</p>
<btn-active ref="btn-active"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">h4</span>&gt;</span>Buttons<span class="hljs-tag">&lt;/<span class="hljs-name">h4</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">active</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>Primary button<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">active</span>&gt;</span>Button<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">h4</span>&gt;</span>Links<span class="hljs-tag">&lt;/<span class="hljs-name">h4</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">active</span> <span class="hljs-attr">href</span>=<span class="hljs-string">"#"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>Primary button<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">active</span> <span class="hljs-attr">to</span>=<span class="hljs-string">"/"</span>&gt;</span>Button<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-comment">&lt;!-- btn-active.vue --&gt;</span>
</code></pre>
<h2 id="disabled-state">Disabled state <a class="header-anchor" href="#disabled-state" aria-hidden="true">&#128279;</a></h2>
<p>Add <code v-pre>disabled</code> to make buttons unclickable.</p>
<btn-disabled ref="btn-disabled"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">h4</span>&gt;</span>Buttons<span class="hljs-tag">&lt;/<span class="hljs-name">h4</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">disabled</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>Primary button<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">disabled</span>&gt;</span>Button<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">h4</span>&gt;</span>Links<span class="hljs-tag">&lt;/<span class="hljs-name">h4</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">disabled</span> <span class="hljs-attr">href</span>=<span class="hljs-string">"#"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>Primary button<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">disabled</span> <span class="hljs-attr">to</span>=<span class="hljs-string">"/"</span>&gt;</span>Button<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-comment">&lt;!-- btn-disabled.vue --&gt;</span>
</code></pre>
<h2 id="checkbox-radio">Checkbox / Radio <a class="header-anchor" href="#checkbox-radio" aria-hidden="true">&#128279;</a></h2>
<p>Add <code v-pre>input-type</code> to render <code v-pre>&lt;btn&gt;</code> as <code v-pre>checkbox</code> or <code v-pre>radio</code> input.</p>
<p><strong>Note</strong>: This needed to work with <code v-pre>&lt;btn-group&gt;</code> for correct styles.</p>
<h3 id="checkbox-example">Checkbox example <a class="header-anchor" href="#checkbox-example" aria-hidden="true">&#128279;</a></h3>
<btn-input-checkbox ref="btn-input-checkbox"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">btn-group</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">input-type</span>=<span class="hljs-string">"checkbox"</span> <span class="hljs-attr">input-value</span>=<span class="hljs-string">"1"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"model"</span>&gt;</span>Checkbox 1<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">input-type</span>=<span class="hljs-string">"checkbox"</span> <span class="hljs-attr">input-value</span>=<span class="hljs-string">"2"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"model"</span>&gt;</span>Checkbox 2<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">input-type</span>=<span class="hljs-string">"checkbox"</span> <span class="hljs-attr">input-value</span>=<span class="hljs-string">"3"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"model"</span>&gt;</span>Checkbox 3<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">input-type</span>=<span class="hljs-string">"checkbox"</span> <span class="hljs-attr">input-value</span>=<span class="hljs-string">"4"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"model"</span> <span class="hljs-attr">disabled</span>&gt;</span>Checkbox 4 (Disabled)<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">btn-group</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">hr</span>/&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">alert</span>&gt;</span>Selected: {{model}}<span class="hljs-tag">&lt;/<span class="hljs-name">alert</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">model</span>: [<span class="hljs-string">'1'</span>]
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-comment">&lt;!-- btn-input-checkbox.vue --&gt;</span>
</code></pre>
<h3 id="radio-example">Radio example <a class="header-anchor" href="#radio-example" aria-hidden="true">&#128279;</a></h3>
<btn-input-radio ref="btn-input-radio"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">btn-group</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">input-type</span>=<span class="hljs-string">"radio"</span> <span class="hljs-attr">input-value</span>=<span class="hljs-string">"1"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"model"</span>&gt;</span>Radio 1<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">input-type</span>=<span class="hljs-string">"radio"</span> <span class="hljs-attr">input-value</span>=<span class="hljs-string">"2"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"model"</span>&gt;</span>Radio 2<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">input-type</span>=<span class="hljs-string">"radio"</span> <span class="hljs-attr">input-value</span>=<span class="hljs-string">"3"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"model"</span>&gt;</span>Radio 3<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">input-type</span>=<span class="hljs-string">"radio"</span> <span class="hljs-attr">input-value</span>=<span class="hljs-string">"4"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"model"</span> <span class="hljs-attr">disabled</span>&gt;</span>Radio 4 (Disabled)<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">btn-group</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">hr</span>/&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">alert</span>&gt;</span>Selected: {{model}}<span class="hljs-tag">&lt;/<span class="hljs-name">alert</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">model</span>: <span class="hljs-string">'1'</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-comment">&lt;!-- btn-input-radio.vue --&gt;</span>
</code></pre>
<h1 id="api-reference">API Reference <a class="header-anchor" href="#api-reference" aria-hidden="true">&#128279;</a></h1>
<h2 id="btn"><a href="https://github.com/wxsms/uiv/blob/master/src/components/button/Btn.js">Btn</a> <a class="header-anchor" href="#btn" aria-hidden="true">&#128279;</a></h2>
<h3 id="props">Props <a class="header-anchor" href="#props" aria-hidden="true">&#128279;</a></h3>
<div class="table-responsive"><table class="table table-bordered table-hover"><thead>
<tr>
<th>Name</th>
<th>Type</th>
<th>Default</th>
<th>Required</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td><code v-pre>type</code></td>
<td>String</td>
<td>default</td>
<td></td>
<td>Button types in Bootstrap. Supported: <code v-pre>default</code>, <code v-pre>primary</code>, <code v-pre>info</code>, <code v-pre>success</code>, <code v-pre>warning</code>, <code v-pre>danger</code>, <code v-pre>link</code>.</td>
</tr>
<tr>
<td><code v-pre>native-type</code></td>
<td>String</td>
<td>button</td>
<td></td>
<td>Native button type. Supported: <code v-pre>button</code>, <code v-pre>submit</code>, <code v-pre>reset</code>.</td>
</tr>
<tr>
<td><code v-pre>size</code></td>
<td>String</td>
<td></td>
<td></td>
<td>Optional button sizes. Supported: <code v-pre>lg</code>, <code v-pre>sm</code>, <code v-pre>xs</code>.</td>
</tr>
<tr>
<td><code v-pre>block</code></td>
<td>Boolean</td>
<td>false</td>
<td></td>
<td>Apply block level style.</td>
</tr>
<tr>
<td><code v-pre>active</code></td>
<td>Boolean</td>
<td>false</td>
<td></td>
<td>Apply active state.</td>
</tr>
<tr>
<td><code v-pre>disabled</code></td>
<td>Boolean</td>
<td>false</td>
<td></td>
<td>Apply disabled state.</td>
</tr>
<tr>
<td><code v-pre>href</code></td>
<td>String</td>
<td></td>
<td></td>
<td>An native link will be created if this prop present.</td>
</tr>
<tr>
<td><code v-pre>target</code></td>
<td>String</td>
<td></td>
<td></td>
<td>Native link prop.</td>
</tr>
<tr>
<td><code v-pre>to</code></td>
<td>String or Object</td>
<td></td>
<td></td>
<td>An Vue-Router link will be created if this prop present.</td>
</tr>
<tr>
<td><code v-pre>replace</code></td>
<td>Boolean</td>
<td>false</td>
<td></td>
<td>Vue-Router link prop.</td>
</tr>
<tr>
<td><code v-pre>append</code></td>
<td>Boolean</td>
<td>false</td>
<td></td>
<td>Vue-Router link prop.</td>
</tr>
<tr>
<td><code v-pre>exact</code></td>
<td>Boolean</td>
<td>false</td>
<td></td>
<td>Vue-Router link prop.</td>
</tr>
<tr>
<td><code v-pre>input-type</code></td>
<td>String</td>
<td></td>
<td></td>
<td>Use this prop to turn btn to checkbox or radio input. Supported types: <code v-pre>checkbox</code> / <code v-pre>radio</code></td>
</tr>
<tr>
<td><code v-pre>input-value</code></td>
<td></td>
<td></td>
<td></td>
<td>The value of input.</td>
</tr>
<tr>
<td><code v-pre>v-model</code></td>
<td></td>
<td></td>
<td></td>
<td>The model of input. Note that this prop <strong>is required</strong> if <code v-pre>input-type</code> present.</td>
</tr>
<tr>
<td><code v-pre>justified</code></td>
<td>Boolean</td>
<td>false</td>
<td></td>
<td>Due to Bootstrap limitation, this prop is needed while using <code v-pre>&lt;btn&gt;</code> in <code v-pre>&lt;btn-group justified&gt;</code>. Otherwise it can be ignored.</td>
</tr>
</tbody>
</table></div><h3 id="slots">Slots <a class="header-anchor" href="#slots" aria-hidden="true">&#128279;</a></h3>
<div class="table-responsive"><table class="table table-bordered table-hover"><thead>
<tr>
<th>Name</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td><code v-pre>default</code></td>
<td>The button body.</td>
</tr>
</tbody>
</table></div><h3 id="events">Events <a class="header-anchor" href="#events" aria-hidden="true">&#128279;</a></h3>
<div class="table-responsive"><table class="table table-bordered table-hover"><thead>
<tr>
<th>Name</th>
<th>Params</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td><code v-pre>click</code></td>
<td></td>
<td>Click event of button / link.</td>
</tr>
</tbody>
</table></div><p><strong>Note</strong>: Use the <code v-pre>.native</code> modifier to capture browser native events such as: <code v-pre>@click.native=&quot;...&quot;</code>, <code v-pre>@mouseover.native=&quot;...&quot;</code>, etc.</p>
</markdown-wrapper></template><script>
  

  
export default {components:{'btn-examples':{"template":"<div class=\"markdown-live-example\"><btn>Default</btn>\n<btn type=\"primary\">Primary</btn>\n<btn type=\"success\">Success</btn>\n<btn type=\"info\">Info</btn>\n<btn type=\"warning\">Warning</btn>\n<btn type=\"danger\">Danger</btn>\n<btn type=\"link\">Link</btn>\n<!-- btn-examples.vue --></div>"},'btn-links':{"template":"<div class=\"markdown-live-example\"><h4>Native links</h4>\n<btn href=\"#\">Default</btn>\n<btn href=\"#\" type=\"primary\">Primary</btn>\n<h4>Vue Router links</h4>\n<btn to=\"/\">Default</btn>\n<btn to=\"/\" type=\"primary\">Primary</btn>\n<!-- btn-links.vue --></div>"},'btn-sizes':{"template":"<div class=\"markdown-live-example\"><p>\n  <btn size=\"lg\" type=\"primary\">Large button</btn>\n  <btn size=\"lg\">Large button</btn>\n</p>\n<p>\n  <btn type=\"primary\">Default button</btn>\n  <btn>Default button</btn>\n</p>\n<p>\n  <btn size=\"sm\" type=\"primary\">Small button</btn>\n  <btn size=\"sm\">Small button</btn>\n</p>\n<p>\n  <btn size=\"xs\" type=\"primary\">Extra small button</btn>\n  <btn size=\"xs\">Extra small button</btn>\n</p>\n<!-- btn-sizes.vue --></div>"},'btn-block':{"template":"<div class=\"markdown-live-example\"><btn block size=\"lg\" type=\"primary\">Block level button</btn>\n<btn block size=\"lg\">Block level button</btn>\n<!-- btn-block.vue --></div>"},'btn-active':{"template":"<div class=\"markdown-live-example\"><h4>Buttons</h4>\n<btn active type=\"primary\">Primary button</btn>\n<btn active>Button</btn>\n<h4>Links</h4>\n<btn active href=\"#\" type=\"primary\">Primary button</btn>\n<btn active to=\"/\">Button</btn>\n<!-- btn-active.vue --></div>"},'btn-disabled':{"template":"<div class=\"markdown-live-example\"><h4>Buttons</h4>\n<btn disabled type=\"primary\">Primary button</btn>\n<btn disabled>Button</btn>\n<h4>Links</h4>\n<btn disabled href=\"#\" type=\"primary\">Primary button</btn>\n<btn disabled to=\"/\">Button</btn>\n<!-- btn-disabled.vue --></div>"},'btn-input-checkbox':{"template":"<div class=\"markdown-live-example\">\n  <btn-group>\n    <btn input-type=\"checkbox\" input-value=\"1\" v-model=\"model\">Checkbox 1</btn>\n    <btn input-type=\"checkbox\" input-value=\"2\" v-model=\"model\">Checkbox 2</btn>\n    <btn input-type=\"checkbox\" input-value=\"3\" v-model=\"model\">Checkbox 3</btn>\n    <btn input-type=\"checkbox\" input-value=\"4\" v-model=\"model\" disabled>Checkbox 4 (Disabled)</btn>\n  </btn-group>\n  <hr/>\n  <alert>Selected: {{model}}</alert>\n</div>",
